import React from 'react';

import './Feedback.css';
import { withRouter } from 'react-router-dom';
import { Select } from 'antd';

const { Option } = Select;

class Feedback extends React.Component {
  cancelForm() {
    this.props.history.push("/about");
  }
  render() {
    return (
      <div>
        <div className="label">
          意见/问题反馈
        </div>
        <div className="block">
          <div className="options-list">
            <div className="option-item">
              <textarea placeholder="填写意见或问题反馈信息"></textarea>
            </div>
            <div className="option-item">
              <div className="main">
                <div className="subject">反馈类型</div>
              </div>
              <div className="operator">
                <Select className="feedback-type-select" defaultValue="advice">
                  <Option value="advice">意见反馈</Option>
                  <Option value="issue">问题反馈</Option>
                </Select>
              </div>
            </div>
            <div className="option-item">
              <div className="main">
                <div className="subject">电子邮件</div>
              </div>
              <div className="operator">
                <input className="input" placeholder="如果您愿意，请提供您的联系方式" />
              </div>
            </div>
          </div>
        </div>
        <div className="button-group">
          <button className="btn submit">提交</button>
          <button className="btn cancel" onClick={this.cancelForm.bind(this)}>取消</button>
        </div>
      </div>
    )
  }
}

export default withRouter(Feedback)