import React from 'react';
import { Radio } from 'antd';
import { confirmAlert } from 'react-confirm-alert';

import BG from '../../bg/bg';

import './Translator.css';

class Translator extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      translator: 'proxy',
      proxyDisable: true,
      baiduDisable: true,
    }
  }
  componentDidMount() {
    let activeLicense = {proxy: false, baidu: false}
    BG.findProxyCurrentLicense().then(res => {
      if (res.result === 'ok' && res.data.appid) {
        this.setState({proxyDisable: false})
        activeLicense.proxy = true
      }
      return BG.findBaiduCurrentLicense()
    }).then(exist => {
      if (exist) {
        this.setState({baiduDisable: false})
        activeLicense.baidu = true
      }
      return BG.getEngine()
    }).then(res => {
      if (res.result === 'ok' && activeLicense[res.data]) {
        this.setState({translator: res.data})
      }
    })
  }
  alert(title, message, handle) {
    confirmAlert({
      title, message,
      closeOnClickOutside: false,
      closeOnEscape: false,
      buttons: [{
        label: '确定',
        onClick: handle || (() => {}),
      }]
    });
  }
  onTranslatorChange(e) {
    const selectVal = e.target.value
    BG.setEngine(selectVal).then(res => {
      if (res.result === 'ok') {
        this.setState({translator: selectVal})
      } else {
        this.alert('设置信息异常', '请尝试重新启动浏览器解决')
      }
    })
  }
  render() {
    return (
      <div>
        <div className="label">请选择要使用的翻译引擎</div>
        <div className="block">
          <div className="options-list">
            <Radio.Group className="radio-group" onChange={this.onTranslatorChange.bind(this)} value={this.state.translator}>
              <div className="option-item">
                <div className="main">
                  <div className="subject with-radio">
                    <Radio value="proxy" disabled={this.state.proxyDisable}>聚合翻译{this.state.proxyDisable && '(未激活)'}</Radio>
                  </div>
                </div>
              </div>
              <div className="option-item">
                <div className="main">
                  <div className="subject with-radio">
                    <Radio value="baidu" disabled={this.state.baiduDisable}>百度翻译{this.state.baiduDisable && '(未激活)'}</Radio>
                  </div>
                </div>
              </div>
            </Radio.Group>
          </div>
        </div>
      </div>
    )
  }
}

export default Translator