import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { withRouter } from 'react-router-dom';
import { Select } from 'antd';
import BG from '../../bg/bg';
import './Activate.css';

const { Option } = Select;
let timer = null

class Activate extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      activateType: 'proxy',
      proxyCardNum: '',
      proxySecret: '',
      baiduAppID: '',
      baiduSecret: '',
      licenseInfo: '输入卡号自动查询',
    }
  }
  translateEngineSelect(value) {
    this.setState({ activateType: value })
  }
  alert(title, message, handle) {
    confirmAlert({
      title, message,
      closeOnClickOutside: false,
      closeOnEscape: false,
      buttons: [{
        label: '完成',
        onClick: handle,
      }]
    });
  }
  activateButtonClick(e) {
    switch (this.state.activateType) {
      case 'proxy': {
        if (!this.state.proxyCardNum || !this.state.proxySecret) {
          this.alert('请完整填写激活信息')
          break;
        }
        BG.proxyTranslatorActivate(
          this.state.proxyCardNum,
          this.state.proxySecret
        ).then(res => {
          let title = ''
          let message = ''
          let func = noop
          if (res.result === 'ok') {
            title = '激活成功'
            func = () => {
              this.props.history.push("/");
            }
          } else {
            title = '激活失败'
            message = res.msg || res.code
          }
          this.alert(title, message, func)
        })
        break;
      }
      case 'baidu': {
        if (!this.state.baiduAppID || !this.state.baiduSecret) {
          this.alert('请完整填写激活信息')
          break;
        }
        BG.baiduTranslatorActivate(
          this.state.baiduAppID,
          this.state.baiduSecret
        ).then(res => {
          if (res.result == 'ok') {
            return this.alert('激活成功')
          } else {
            return this.alert('激活失败')
          }
        })
        break;
      }
      default: {
        // unsupported activate type
        this.alert('暂不支持此类型')
      }
    }
  }
  proxyCardInputChange(e) {
    const val = e.target.value
    this.setState({proxyCardNum: val})

    if (!val) {
      this.setState({licenseInfo: '输入卡号自动查询'})
      return
    } else {
      this.setState({licenseInfo: '查询中...'})
    }

    if (timer) clearTimeout(timer)
    timer = setTimeout(() => {
      BG.findProxyTranslatorlicenseInfo(val).then(res => {
        if (res.result === 'ok' && res.data) {
          this.setState({licenseInfo: res.data.name || '未找到对应信息'})
        } else {
          this.setState({licenseInfo: '未找到对应信息'})
        }
      })
    }, 500)
  }
  proxySecretInputChange(e) {
    this.setState({proxySecret: e.target.value})
  }
  baiduAppIDInputChange(e) {
    this.setState({baiduAppID: e.target.value})
  }
  baiduSecretInputChange(e) {
    this.setState({baiduSecret: e.target.value})
  }
  render() {
    return (
      <div>
        <div className="label">
          授权激活
        </div>
        <div className="block">
          <div className="options-list">
            <div className="option-item">
              <div className="main">
                <div className="subject">翻译引擎类型</div>
              </div>
              <div className="operator">
                <Select defaultValue="proxy" onChange={this.translateEngineSelect.bind(this)}>
                  <Option value="proxy">聚合翻译引擎</Option>
                  <Option value="baidu">百度翻译引擎</Option>
                </Select>
              </div>
            </div>
            <div className="option-item">
              {
                this.state.activateType === 'proxy' ?
                <div className="proxy-license notice">
                  <p>目前该引擎为内测期间，不开放公开注册。详情请咨询开发者，或选择使用百度翻译引擎。</p>
                  <p>老用户直接联系开发者升级 License</p>
                  <b>该翻译引擎激活码激活后会绑定当前设备(已登录浏览器的用户会绑定Google账号)</b>
                </div> :
                <div className="baidu-license notice">
                  <p>使用百度翻译引擎需要申请百度翻译API使用权限，申请地址https://api.fanyi.baidu.com/，申请完成后，从 “管理控制台” 获取 AppID 和 Secret 填入即可激活。</p>
                  <p>使用百度翻译引擎，具体使用量由百度进行统计，具体资费参考百度提供的信息（目前百度提供免费版接口）</p>
                  <b>您的 AppID 和 Secret 仅在本地保存，本应用不会收集您的 AppID 和 Secret 信息，Secret 信息也不会在网络中传输。</b>
                </div>
              }
            </div>
            {
              this.state.activateType === 'proxy' ?
              <div className="proxy-translate-license">
                <div className="option-item">
                  <div className="main">
                    <div className="subject">卡号</div>
                  </div>
                  <div className="operator">
                    <input
                      className="input"
                      placeholder="请输入聚合翻译激活卡卡号"
                      value={this.state.proxyCardNum}
                      onChange={this.proxyCardInputChange.bind(this)}
                      />
                  </div>
                </div>
                <div className="option-item">
                  <div className="main">
                    <div className="subject">密钥</div>
                  </div>
                  <div className="operator">
                    <input
                      className="input"
                      placeholder="请输入聚合翻译激活卡密钥"
                      value={this.state.proxySecret}
                      onChange={this.proxySecretInputChange.bind(this)}
                      />
                  </div>
                </div>
                <div className="option-item">
                  <div className="main">
                    <div className="subject license-info">授权信息：<span>{this.state.licenseInfo}</span></div>
                  </div>
                </div>
              </div> :
              <div className="baidu-translate-license">
                <div className="option-item">
                  <div className="main">
                    <div className="subject">AppID</div>
                  </div>
                  <div className="operator">
                    <input
                      className="input"
                      placeholder="请输入百度翻译开发者AppID"
                      value={this.state.baiduAppID}
                      onChange={this.baiduAppIDInputChange.bind(this)}
                      />
                  </div>
                </div>
                <div className="option-item">
                  <div className="main">
                    <div className="subject">Secret</div>
                  </div>
                  <div className="operator">
                    <input
                      className="input"
                      placeholder="请输入百度翻译开发者Secret"
                      value={this.state.baiduSecret}
                      onChange={this.baiduSecretInputChange.bind(this)}
                      />
                  </div>
                </div>
              </div>
            }
          </div>
        </div>
        <div className="button-group">
          <button className="btn activate" onClick={this.activateButtonClick.bind(this)}>激活</button>
        </div>
      </div>
    )
  }
}

export default withRouter(Activate)

function noop() {return}